import { Component, h, Prop, Element } from '@stencil/core';
import { common } from '../../global/data/common';

@Component({
      tag: 'ks-form'
})
export class Form {
    @Prop() name: string;
    @Prop() action: string;
    @Prop() method: string;

    @Element() root: HTMLElement;

	render() {
        const strings = common.get("translations");

		return(
            <form action={this.action} method={this.method} enctype="multipart/form-data" onSubmit={(event)=>this.Submit(event)}>
                <slot/>

                <input class="uk-button uk-button-danger uk-width-1-1 uk-margin-medium-top" style={{padding: "8px 40px"}} type="submit" value={strings.inputSubmitForm} formnovalidate></input>
            </form>
		);
    }

    async Submit(event: Event) {
        event.preventDefault();

        const form = this.root.querySelector("form");
        const inputs = form.querySelectorAll("ks-input-text, ks-input-textarea, ks-input-check, ks-input-number, ks-input-date, ks-input-select");


        let valid = true;
        for (let i = 0; i < inputs.length; i++) {
            const input = inputs[i] as any;
            
            await input.Validate();

            const isValid = await input.IsValid()

            if(!isValid) {
                valid = false;
            }
        }

        if(valid) form.submit();
    }
}
